import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";
import "./App.css";
import SignInSide from "./pages/Login";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import {
  createBrowserRouter,
  RouterProvider,
  redirect,
  Navigate,
} from "react-router-dom";
import Dashboard from "./pages/Dashboard";
import Stack from "@mui/material/Stack";
import FindDonorByPicture from "./pages/FindDonorByPicture";
import DonorsManagement from "./pages/DonorsManagement";
import UserTablePage from "./pages/UsersTablePage";

function dashboardLoader() {
  const token = sessionStorage.getItem("authToken");

  if (token) {
    return null;
  } else {
    return redirect("/");
  }
}

function App() {
  const defaultTheme = createTheme({ palette: { mode: "light" } });
  const router = createBrowserRouter([
    {
      path: "/",
      element: <SignInSide />,
    },
    {
      path: "/dashboard",
      element: <Dashboard />,
      loader: dashboardLoader,
      children: [
        {
          path: "",
          element: <Navigate to="donors" replace />,
        },
        {
          path: "donors",
          element: <FindDonorByPicture />,
        },
        {
          path: "donors-mgmt",
          element: <DonorsManagement />,
        },
        {
          path: "staff",
          element: <UserTablePage />,
        },
      ],
    },
  ]);

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <div className="App">
        <Stack
          direction="column"
          component="main"
          sx={[
            {
              justifyContent: "space-between",
            },
            (theme) => ({
              backgroundImage:
                "radial-gradient(ellipse at 70% 51%, hsl(210, 100%, 97%), hsl(0, 0%, 100%))",
              backgroundSize: "cover",
              height: { xs: "auto", md: "100dvh" },
              pb: { xs: 12, sm: 0 },
              ...theme.applyStyles("dark", {
                backgroundImage:
                  "radial-gradient(at 70% 51%, hsla(210, 100%, 16%, 0.5), hsl(220, 30%, 5%))",
              }),
            }),
          ]}
        >
          <RouterProvider router={router} />
        </Stack>
      </div>
    </ThemeProvider>
  );
}

export default App;
