import Stack from "@mui/material/Stack";
import {
  Alert,
  Badge,
  BottomNavigation,
  BottomNavigationAction,
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  Chip,
  CircularProgress,
  IconButton,
  Paper,
  Tooltip,
  Typography,
} from "@mui/material";
import Grid from "@mui/material/Grid2";

import Webcam from "react-webcam";
import { useEffect, useRef, useState } from "react";
import CameraIcon from "@mui/icons-material/Camera";
import Fab from "@mui/material/Fab";
import ReplayIcon from "@mui/icons-material/Replay";
import YoutubeSearchedForIcon from "@mui/icons-material/YoutubeSearchedFor";
import APP_CONFIG from "../config";
import DonorRegistrationModal from "../components/DonorRegistrationModal";
import { apiRequest } from "../helpers/apiRequest";
import DonorCard from "../components/DonorCard";

export default function DonorsManagement() {
  const [donors, setDonors] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchDonors = async () => {
      try {
        const response = await apiRequest(`${APP_CONFIG.apiEndpoint}/donor`, {
          method: "GET",
        });

        if (!response.ok) {
          throw new Error("Failed to fetch donors");
        }

        const data = await response.json();
        setDonors(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchDonors();
  }, []);

  return loading ? (
    <CircularProgress />
  ) : (
    <Grid container spacing={2}>
      {error && <Typography color="error">{error}</Typography>}
      {donors.map((donor) => (
        <Grid item key={donor._id} xs={12} sm={6} md={4}>
          <DonorCard donor={donor} showFullDetails editable />
        </Grid>
      ))}
    </Grid>
  );
}
