import React, { useEffect, useState } from "react";
import {
  Alert,
  Box,
  Button,
  CircularProgress,
  Modal,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import APP_CONFIG from "../config";
import { apiRequest } from "../helpers/apiRequest";

export default function DonorRegistrationModal({
  open,
  handleClose,
  onSubmit,
  imageSrc,
}) {
  const [donorDetails, setDonorDetails] = useState({
    name: "",
    phone: "",
    address: "",
  });
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(null);
  const [imageUrl, setImageUrl] = useState(null);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setDonorDetails({
      ...donorDetails,
      [name]: value,
    });
  };

  const handleSubmit = async () => {
    setLoading(true);
    setError(null);
    setErrors({});

    try {
      let uploadedImageUrl = imageUrl;
      const token = sessionStorage.getItem("authToken");

      if (!uploadedImageUrl) {
        // Step 1: Upload image
        const imageResponse = await fetch(imageSrc);
        const imageBlob = await imageResponse.blob();

        const imageData = new FormData();
        imageData.append("images", imageBlob, "captured_image.jpg");

        const uploadResponse = await apiRequest(
          `${APP_CONFIG.apiEndpoint}/upload`,
          {
            method: "POST",
            headers: {
              Authorization: `Bearer ${token}`,
            },
            body: imageData,
          }
        );

        const uploadResult = await uploadResponse.json();
        if (!uploadResponse.ok) {
          throw new Error(uploadResult.message || "Image upload failed");
        }

        // Store the uploaded image URL in the state
        setImageUrl(uploadResult.image);
        uploadedImageUrl = uploadResult.image;
      }

      // Step 2: Send donor details along with uploaded image URL
      const donorResponse = await apiRequest(
        `${APP_CONFIG.apiEndpoint}/donor`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({
            ...donorDetails,
            profilePictureUri: uploadedImageUrl,
            idCardUri: uploadedImageUrl,
          }),
        }
      );

      const donorResult = await donorResponse.json();

      if (donorResponse.ok) {
        setSuccess(true);
        setTimeout(() => {
          handleClose();
        }, 10000);
      } else {
        if (donorResult.error) {
          setErrors(donorResult.error);
          setError(donorResult.message || "Failed to add donor");
        } else {
          setError(donorResult.message || "Failed to add donor");
        }
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (!open) {
      setSuccess(false);
      setError(null);
      setErrors({});
      setImageUrl(null); // Clear the image URL when the modal is closed
    }
  }, [open]);

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="register-donor-title"
      aria-describedby="register-donor-description"
    >
      <Box
        sx={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: 400,
          bgcolor: "background.paper",
          boxShadow: 24,
          p: 4,
          borderRadius: 2,
        }}
      >
        <Typography id="register-donor-title" variant="h6" component="h2">
          Register Donor
        </Typography>
        {success ? (
          <Alert severity="success" sx={{ mt: 2 }}>
            Donor registered successfully! This modal will close in 10 seconds.
          </Alert>
        ) : (
          <Stack spacing={2} mt={2}>
            {error && (
              <Alert severity="error" sx={{ mb: 2 }}>
                {error}
              </Alert>
            )}
            <TextField
              label="Name"
              name="name"
              value={donorDetails.name}
              onChange={handleInputChange}
              error={!!errors.name}
              helperText={errors.name}
              fullWidth
              required
            />
            <TextField
              label="Phone"
              name="phone"
              value={donorDetails.phone}
              onChange={handleInputChange}
              error={!!errors.phone}
              helperText={errors.phone}
              fullWidth
              required
            />
            <TextField
              label="Address"
              name="address"
              value={donorDetails.address}
              onChange={handleInputChange}
              error={!!errors.address}
              helperText={errors.address}
              fullWidth
              required
            />
            <Button
              variant="contained"
              color="primary"
              onClick={handleSubmit}
              disabled={loading}
            >
              {loading ? <CircularProgress size={24} /> : "Submit"}
            </Button>
          </Stack>
        )}
      </Box>
    </Modal>
  );
}
