import React, { useEffect, useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Paper,
  Typography,
  Box,
  Tooltip,
  IconButton,
} from "@mui/material";
import { apiRequest } from "../helpers/apiRequest";
import SetPasswordModal from "../components/SetPasswordModal";
import APP_CONFIG from "../config";
import { Add as AddIcon } from "@mui/icons-material";
import RegisterUserModal from "../components/RegisterUserModal";

function UserTablePage() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [selectedUser, setSelectedUser] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [registerModalOpen, setRegisterModalOpen] = useState(false);

  const fetchUsers = async () => {
    try {
      const response = await apiRequest(`${APP_CONFIG.apiEndpoint}/users`, {
        method: "GET",
      });

      if (!response.ok) {
        throw new Error("Failed to fetch users");
      }

      const data = await response.json();
      setUsers(data);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const handleSetPassword = (user) => {
    setSelectedUser(user);
    setModalOpen(true);
  };

  const handleCloseModal = () => {
    setSelectedUser(null);
    setModalOpen(false);
  };
  const handleOpenRegisterModal = () => {
    setRegisterModalOpen(true);
  };

  const handleCloseRegisterModal = () => {
    setRegisterModalOpen(false);
  };

  return (
    <Box display="flex" flexDirection="column" gap={10} mt={4}>
      <Typography variant="h4" gutterBottom>
        Registered Users
      </Typography>
      <Tooltip title="Register New User">
        <IconButton
          color="primary"
          onClick={handleOpenRegisterModal}
          sx={{ marginBottom: 2 }}
        >
          <AddIcon />
        </IconButton>
      </Tooltip>
      {loading ? (
        <Typography>Loading users...</Typography>
      ) : error ? (
        <Typography color="error">{error}</Typography>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Office/Role</TableCell>
                <TableCell>Email</TableCell>
                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {users.map((user) => (
                <TableRow key={user._id}>
                  <TableCell>{user.fullName}</TableCell>
                  <TableCell>
                    {user.staffRank} ({user.role})
                  </TableCell>
                  <TableCell>{user.email}</TableCell>
                  <TableCell>
                    <Button
                      variant="contained"
                      color="primary"
                      onClick={() => handleSetPassword(user)}
                    >
                      Set New Password
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {selectedUser && (
        <SetPasswordModal
          user={selectedUser}
          open={modalOpen}
          onClose={handleCloseModal}
        />
      )}

      <RegisterUserModal
        open={registerModalOpen}
        onClose={handleCloseRegisterModal}
        onSuccess={fetchUsers}
      />
    </Box>
  );
}

export default UserTablePage;
