import React, { useState } from "react";
import {
  Modal,
  Box,
  TextField,
  Button,
  Paper,
  Typography,
  Alert,
} from "@mui/material";
import { apiRequest } from "../helpers/apiRequest";
import APP_CONFIG from "../config";

function EditDonorModal({ donor, open, onClose }) {
  const [updatedDonor, setUpdatedDonor] = useState({
    name: donor.name,
    email: donor.email,
    phone: donor.phone,
    address: donor.address,
  });
  const [errors, setErrors] = useState({});
  const [generalError, setGeneralError] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUpdatedDonor({ ...updatedDonor, [name]: value });
  };

  const handleSave = async () => {
    try {
      const response = await apiRequest(
        `${APP_CONFIG.apiEndpoint}/donor/${donor._id}`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(updatedDonor),
        }
      );

      if (!response.ok) {
        if (response.status === 400) {
          // Assuming the API returns a JSON object with field errors
          const errorData = await response.json();
          setErrors(errorData.error || {});
        } else {
          throw new Error("Failed to update donor");
        }
        return;
      }

      setSuccessMessage("Donor details updated successfully!");

      setTimeout(() => {
        setSuccessMessage("");
        onClose();
      }, 5000);
    } catch (error) {
      console.error("Error updating donor:", error);
      setGeneralError(error.message);
    }
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box
        sx={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: 400,
          bgcolor: "background.paper",
          boxShadow: 24,
          p: 4,
        }}
      >
        <Typography variant="h6">Edit Donor</Typography>
        {successMessage && <Alert severity="success">{successMessage}</Alert>}
        {generalError && <Alert severity="error">{generalError}</Alert>}

        <TextField
          label="Name"
          name="name"
          value={updatedDonor.name}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          error={!!errors.name}
          helperText={errors.name}
        />
        <TextField
          label="Email"
          name="email"
          value={updatedDonor.email}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          error={!!errors.email}
          helperText={errors.email}
        />
        <TextField
          label="Phone"
          name="phone"
          value={updatedDonor.phone}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          error={!!errors.phone}
          helperText={errors.phone}
        />
        <TextField
          label="Address"
          name="address"
          value={updatedDonor.address}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          error={!!errors.address}
          helperText={errors.address}
        />
        <Button onClick={handleSave}>Save</Button>
      </Box>
    </Modal>
  );
}

export default EditDonorModal;
