import React, { useEffect } from "react";
import {
  Card,
  CardContent,
  Typography,
  Button,
  Avatar,
  CardActions,
  CardMedia,
  Chip,
  Alert,
  Modal,
  Paper,
} from "@mui/material";
import { Box } from "@mui/system";
import { useState } from "react";
import APP_CONFIG from "../config";
import { apiRequest } from "../helpers/apiRequest";
import EditDonorModal from "./EditDonorModal";

export default function DonorCard({ donor, showFullDetails, editable }) {
  const [donationSuccess, setDonationSuccess] = useState(null);
  const [donating, setDonating] = useState(false);
  const [isDonatedToday, setIsDonatedToday] = useState(false);
  const [modalOpen, setModalOpen] = useState(false);
  const [isEditModalOpen, setEditModalOpen] = useState(false);

  const handleEditClick = () => {
    setEditModalOpen(true);
  };

  const handleDonate = async () => {
    try {
      setDonating(true);
      const response = await apiRequest(
        `${APP_CONFIG.apiEndpoint}/donor/${donor._id}/donate`,
        {
          method: "POST",
        }
      );

      if (response.ok) {
        setDonationSuccess(true);
        setIsDonatedToday(true);
      } else {
        setDonationSuccess(false);
      }
    } catch (error) {
      console.error("Donation error:", error);
      setDonationSuccess(false);
    } finally {
      setDonating(false);
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const formattedDate = date.toLocaleString("en-GB", {
      year: "numeric",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false,
    });
    return formattedDate.replace(",", "");
  };

  useEffect(() => {
    const lastDonationDate =
      donor.donations.length > 0
        ? new Date(donor.donations[donor.donations.length - 1].date)
        : null;

    const isLastDonationToday = lastDonationDate
      ? lastDonationDate.toDateString() === new Date().toDateString()
      : false;

    setIsDonatedToday(isLastDonationToday);
  }, [donor.donations]);

  const handleCardClick = () => {
    if (showFullDetails) {
      setModalOpen(true);
    }
  };

  const handleClose = () => setModalOpen(false);

  console.log("fsadfasd:::", donor.searchHistory);

  return (
    <>
      <Card
        sx={{
          maxWidth: 345,
          m: 2,
          cursor: showFullDetails ? "pointer" : "default",
          transition: "transform 0.2s, box-shadow 0.2s",
          "&:hover": {
            transform: showFullDetails ? "scale(1.05)" : "none",
            boxShadow: showFullDetails ? 6 : 1,
          },
        }}
      >
        <Box onClick={handleCardClick}>
          <CardMedia
            component="img"
            height="250"
            image={`${APP_CONFIG.uploadsEndpoint}${donor.profilePicture}`}
            alt={donor.name}
          />
          <CardContent>
            <Box display="flex" flexDirection="column" alignItems="center">
              <Box display="flex" alignItems="center">
                {/* <Avatar
            alt={donor.name}
            src={`${APP_CONFIG.apiEndpoint}${donor.profilePicture}`}
            sx={{ mr: 2 }}
          /> */}
                <Typography variant="h5" component="div">
                  {donor.name}
                </Typography>
              </Box>
              <Typography variant="body2" color="text.secondary">
                Phone: {donor.phone}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Address: {donor.address}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {donor.donationCount > 0 ? (
                  <>
                    <Alert icon={false} color="primary" size="small">
                      {`Donated ${
                        donor.donationCount
                      } time(s). Last donated on ${formatDate(
                        donor.donations[donor.donations.length - 1].date
                      )}`}
                    </Alert>
                    <Alert icon={false} color="primary" size="small">
                      {`Last donation(s) were handled by ${Array.from(
                        new Set(
                          donor.donations
                            .slice(-3)
                            .map(({ handledBy: { fullName } }) => fullName)
                        )
                      ).join(", ")}`}
                    </Alert>
                  </>
                ) : (
                  <Chip color="primary" label="No Donations" size="small" />
                )}
              </Typography>
              {donationSuccess !== null && (
                <Typography
                  variant="body2"
                  color={donationSuccess ? "success.main" : "error.main"}
                >
                  {donationSuccess
                    ? "Donation recorded successfully!"
                    : "Failed to record donation."}
                </Typography>
              )}
            </Box>
          </CardContent>
        </Box>
        <CardActions>
          <Button
            disabled={donating || donationSuccess || isDonatedToday}
            variant="contained"
            color="primary"
            onClick={handleDonate}
          >
            Record Donation
          </Button>
          {editable && (
            <Button
              variant="outlined"
              color="primary"
              onClick={handleEditClick}
            >
              Edit
            </Button>
          )}
        </CardActions>
      </Card>
      <EditDonorModal
        donor={donor}
        open={isEditModalOpen}
        onClose={() => setEditModalOpen(false)}
      />
      <Modal open={modalOpen} onClose={handleClose}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: 400,
            bgcolor: "background.paper",
            boxShadow: 24,
            p: 4,
          }}
        >
          <Typography variant="h6" component="h2">
            {donor.name}
          </Typography>
          <Typography sx={{ mt: 2 }}>Email: {donor.email}</Typography>
          <Typography sx={{ mt: 2 }}>Phone: {donor.phone}</Typography>
          <Typography sx={{ mt: 2 }}>Address: {donor.address}</Typography>
          <Typography sx={{ mt: 2 }}>
            Donations: {donor.donationCount}
          </Typography>
          <Typography sx={{ mt: 2 }}>
            Created by: {donor.createdBy.fullName}
          </Typography>
          <Paper sx={{ mt: 3, p: 2 }}>
            <Typography variant="h6">Donations History:</Typography>
            {donor.donations.map((donation, index) => (
              <Box key={index} sx={{ mt: 1 }}>
                <Typography variant="body2">
                  Date: {formatDate(donation.date)}
                </Typography>
                <Typography variant="body2">
                  Handled by: {donation.handledBy.fullName}
                </Typography>
              </Box>
            ))}
          </Paper>
          <Paper
            elevation={2}
            sx={{ p: 2 }}
            style={{
              padding: "16px",
              marginTop: "16px",
              maxHeight: "230px",
              overflowY: "auto",
            }}
          >
            <Typography variant="h6">Search History</Typography>
            {donor.searchHistory
              .sort((a, b) => new Date(b.searchedAt) - new Date(a.searchedAt))
              .map((history) => (
                <Box key={history._id} sx={{ mb: 1 }}>
                  <Typography variant="body2">
                    Searched At: {formatDate(history.searchedAt)}
                  </Typography>
                  <Typography variant="body2">
                    Searcher: {history.searcher.fullName} (
                    {history.searcher.email})
                  </Typography>
                </Box>
              ))}
          </Paper>
          <CardMedia
            component="img"
            height="250"
            image={`${APP_CONFIG.uploadsEndpoint}${donor.idCard}`}
            alt="ID Card"
            sx={{ mt: 2 }}
          />
          <Button variant="contained" onClick={handleClose} sx={{ mt: 2 }}>
            Close
          </Button>
        </Box>
      </Modal>
    </>
  );
}
