import Stack from "@mui/material/Stack";
import {
  Alert,
  Badge,
  BottomNavigation,
  BottomNavigationAction,
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  Chip,
  IconButton,
  Paper,
  Tooltip,
  Typography,
} from "@mui/material";
import Grid from "@mui/material/Grid2";

import Webcam from "react-webcam";
import { useRef, useState } from "react";
import CameraIcon from "@mui/icons-material/Camera";
import Fab from "@mui/material/Fab";
import ReplayIcon from "@mui/icons-material/Replay";
import YoutubeSearchedForIcon from "@mui/icons-material/YoutubeSearchedFor";
import APP_CONFIG from "../config";
import DonorRegistrationModal from "../components/DonorRegistrationModal";
import { apiRequest } from "../helpers/apiRequest";
import DonorCard from "../components/DonorCard";

export default function FindDonorByPicture() {
  const webcamRef = useRef(null);
  const [imageSrc, setImageSrc] = useState(null);
  const [error, setError] = useState(null);
  const [noMatch, setNoMatch] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [matchingDonors, setMatchingDonors] = useState([]);

  const capture = () => {
    const image = webcamRef.current.getScreenshot();
    setImageSrc(image);
  };

  const retakePicture = () => {
    setImageSrc(null);
    setError(null); // Clear previous error
    setNoMatch(false); // Clear previous no match message
    setMatchingDonors([]);
  };

  const uploadImage = async () => {
    if (imageSrc) {
      try {
        const response = await fetch(imageSrc);
        const blob = await response.blob();
        const token = sessionStorage.getItem("authToken");

        setError(null);
        setMatchingDonors([]);
        setNoMatch(false);

        const formData = new FormData();
        formData.append("picture", blob, "captured_image.jpg");

        const uploadResponse = await apiRequest(
          `${APP_CONFIG.apiEndpoint}/donor/search`,
          {
            method: "POST",
            headers: {
              Authorization: `Bearer ${token}`,
            },
            body: formData,
          }
        );

        const result = await uploadResponse.json();

        if (uploadResponse.ok) {
          if (result.matchingDonors?.length > 0) {
            setMatchingDonors(result.matchingDonors);

            console.log("Upload successful:", result);
          } else {
            // Handle the successful result as needed

            setNoMatch(true);
          }
        } else {
          if (result.type === "ValidationFailedError") {
            setError(result.info);
          } else {
            setError("An unknown error occurred.");
          }
        }

        console.log("Upload successful:", result);
      } catch (error) {
        console.error("Error uploading image:", error);
      }
    }
  };

  const handleRegisterDonor = () => {
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
  };

  return (
    <Box
      sx={{ textAlign: "center", mt: 4 }}
      display="flex"
      flexDirection="column"
    >
      {imageSrc ? (
        <Box sx={{ position: "relative", width: "400px" }}>
          <img
            src={imageSrc}
            alt="Captured"
            style={{ maxWidth: "100%", borderRadius: 10 }}
          />
          <Box display="flex" flexDirection="row" gap={1}>
            <Button variant="contained" color="primary" onClick={uploadImage}>
              <YoutubeSearchedForIcon /> Find Donor
            </Button>
            <Button variant="outlined" onClick={retakePicture}>
              <ReplayIcon /> Retake
            </Button>
          </Box>
          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}
        </Box>
      ) : (
        <Box sx={{ position: "relative", width: "400px" }}>
          <Webcam
            audio={false}
            ref={webcamRef}
            screenshotFormat="image/jpeg"
            width="100%"
            videoConstraints={{
              facingMode: "user",
            }}
            style={{ borderRadius: 10 }}
          />
          <Box
            sx={{
              position: "absolute",
              bottom: "23%",
              left: "20%",
              width: "60%",
              height: "60%",
              border: "4px solid yellow",
              boxSizing: "border-box",
              pointerEvents: "none",
            }}
          />
          <Tooltip title="Capture Donor">
            <Fab
              sx={{
                position: "absolute",
                bottom: "4.5%",
                left: "44%",
              }}
              variant="contained"
              color="secondary"
              size="medium"
              onClick={capture}
            >
              <CameraIcon />
            </Fab>
          </Tooltip>
        </Box>
      )}
      <Box>
        {noMatch && (
          <Alert severity="info" sx={{ mb: 2 }}>
            No matching donor found. You can register a new donor.
            <Button
              variant="contained"
              color="primary"
              onClick={handleRegisterDonor}
              sx={{ mt: 1 }}
            >
              Register Donor
            </Button>
          </Alert>
        )}
      </Box>

      <Box>
        {matchingDonors.length > 0 && (
          <Grid container spacing={2} mt={4}>
            {matchingDonors.map((donor) => (
              <Grid item xs={12} sm={6} md={4} key={donor._id}>
                <DonorCard donor={donor} />
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
      <DonorRegistrationModal
        open={openModal}
        handleClose={handleCloseModal}
        imageSrc={imageSrc}
      />
    </Box>
  );
}
