import React, { useState } from "react";
import {
  Modal,
  Box,
  TextField,
  Button,
  Typography,
  MenuItem,
  Alert,
} from "@mui/material";
import { apiRequest } from "../helpers/apiRequest";
import APP_CONFIG from "../config";

const defaultUserState = {
  email: "",
  fullName: "",
  password: "",
  role: "user",
  staffRank: "",
};

function RegisterUserModal({ open, onClose, onSuccess }) {
  const [newUser, setNewUser] = useState(defaultUserState);
  const [error, setError] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewUser({ ...newUser, [name]: value });
  };

  const handleRegister = async () => {
    setError("");
    try {
      const response = await apiRequest(
        `${APP_CONFIG.apiEndpoint}/auth/register`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ ...newUser, email: newUser.email.trim() }),
        }
      );

      if (!response.ok) {
        throw new Error("Failed to register user");
      }

      setSuccessMessage("User registered successfully!");
      setNewUser(defaultUserState);
      onSuccess();

      // Close the modal after 5 seconds
      setTimeout(() => {
        setSuccessMessage("");
        onClose();
      }, 5000);
    } catch (error) {
      console.error("Error registering user:", error);
      setError(error.message);
    }
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box
        sx={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: 400,
          bgcolor: "background.paper",
          boxShadow: 24,
          p: 4,
        }}
      >
        <Typography variant="h6">Register New User</Typography>
        {successMessage && <Alert severity="success">{successMessage}</Alert>}
        {error && <Alert severity="error">{error}</Alert>}
        <TextField
          label="Email"
          name="email"
          value={newUser.email}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Full Name"
          name="fullName"
          value={newUser.fullName}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Password"
          name="password"
          type="password"
          value={newUser.password}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <TextField
          label="Role"
          name="role"
          value={newUser.role}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
          select
        >
          <MenuItem value="admin">Admin</MenuItem>
          <MenuItem value="user">User</MenuItem>
        </TextField>
        <TextField
          label="Staff Rank"
          name="staffRank"
          value={newUser.staffRank}
          onChange={handleInputChange}
          fullWidth
          margin="normal"
        />
        <Button variant="contained" color="primary" onClick={handleRegister}>
          Register
        </Button>
      </Box>
    </Modal>
  );
}

export default RegisterUserModal;
